        this.craftCardInfoDTOList[index].close=false;
      },
      closefalse(index){
        this.craftCardInfoDTOList[index].close=true;
      },
      async Addmessagebystudent2(talktext) {//添加讨论消息
        let {teachingid} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddstudentmessage(teachingid, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Studentlistmessage2(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore2(pagenum){
        this.Studentlistmessage2(pagenum)
      },
      async Studentlistmessage2(pageNum) {//得到讨论消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList2=[];
          this.startId=0;
          var size = 5;
        };
        try {
          let response = await fetchStudentlistbyteachindid(this.items.teaching.id, pageNum, 5, this.startId2);
          if (response.status.code ==0) {
            this.totalsi2 = response.result.teachingMessageInfoDTOBaseListDTO.total;
             this.workMessageInfoDTOList2=response.result.teachingMessageInfoDTOBaseListDTO.list.reverse().concat(this.workMessageInfoDTOList2)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      deletemessage2(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage2(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage2(id, index) {//删除讨论消息
        this.loading = true;
        try {
          let response = await fetchtDeletestudentmessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList2.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },

      delworkFileDTO(id,index){
        this.$confirm('确定要删除作业附件吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletewrokfile(id,index);
          })
          .catch(() => {});
      },
      async Deletewrokfile(id, index) {//删除作业附件
        this.loading = true;
        try {
          let response = await fetchDeletewrokfile(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.items.workFileDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async add_img2(event){ //工序简图
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          let reader =new FileReader();
          let img1=event.target.files[0];
          let form = new FormData(); 
          form.append('file',img1,img1.name);
          form.append('workId',this.id);
          try {
            let response = await post3('/processfile/addworkfile/',form);
            if (response.status.code ==0) {
              this.pageNum = 1;
              this.$message({
                message: '上传成功',
                type: 'success'
              });
              this.craftCardInfoDTOList[event.target.dataset.index].processFileId=response.result.id;
              this.craftCardInfoDTOList[event.target.dataset.index].processFile.path=response.result.path;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) { 
            console.log(e);
          } finally {
            loading.close()
            this.filevalue2 = '';
          } 
        },
      async add_img(event){ //学生附件上传
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          let reader =new FileReader();
          let img1=event.target.files[0];
          let form = new FormData(); 
          form.append('file',img1,img1.name);
          form.append('workId',this.id);
          try {
            let response = await post3('/workfile/addworkfile/',form);
            if (response.status.code ==0) {
              this.pageNum = 1;
              this.$message({
                message: '上传成功',
                type: 'success'
              });
              this.Getworkinfobyid();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) { 
            console.log(e);
          } finally {
            loading.close()
            this.filevalue = '';
          } 
        },
      timers() {//定时保存
// 　　　　this.timer = setInterval( () => {
//           this.savework(2)
// 　　　　}, 10000);
      },
      //one
      addafter(index){//向后添加
        let data = {
          "processId": "",
          "surfaceId": "",
          "close":false,
          "processFileId": "",
          "processFile":{
                  'name': "",'path': "",'size': ""
                },
          "name": "",
          "sort": "",
          "workingPositionList": [{
              "workingPositionId": "",
              "sort": "",
              "workingStepList": [{
                  "workingStepId": "",
                  "tool": "",
                  "cutter": "",
                  "cutDepth": "",
                  "mainshaftSpeed": "",
                  "fixture": "",
                  "feedingDepth": "",
                  "load": "",
                  "cutSpeed": "",
                  "basicTime": "",
                  "assistTime": "",
                  "workspaceServiceTime": "",
                  "sort": ""
              }]
          }]
        }
        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      delcarts(index){//删除卡片
        this.craftCardInfoDTOList.splice(index, 1)
      },
      removefront(index){//向前移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);
        this.craftCardInfoDTOList.splice(index-1, 0, data)
      },
      removeafter(index){//向后移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);

        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      //two
      addafter1(index,index1){//向后添加
        let data = {
              "workingPositionId": "",
              "sort": "",
              "workingStepList": [{
                  "workingStepId": "",
                  "tool": "",
                  "cutter": "",
                  "cutDepth": "",
                  "mainshaftSpeed": "",
                  "fixture": "",
                  "feedingDepth": "",
                  "load": "",
                  "cutSpeed": "",
                  "basicTime": "",
                  "assistTime": "",
                  "workspaceServiceTime": "",
                  "sort": ""
              }]
          }
        this.craftCardInfoDTOList[index].workingPositionList.splice(index1+1, 0, data)
      },
      delcarts1(index,index1){//删除卡片
        this.craftCardInfoDTOList[index].workingPositionList.splice(index1, 1)
      },
      removefront1(index,index1){//向前移动
        let data = this.craftCardInfoDTOList[index].workingPositionList[index1];
        this.craftCardInfoDTOList[index].workingPositionList.splice(index1, 1);
        this.craftCardInfoDTOList[index].workingPositionList.splice(index1-1, 0, data)
      },
      removeafter1(index,index1){//向后移动
        let data = this.craftCardInfoDTOList[index].workingPositionList[index1];
        this.craftCardInfoDTOList[index].workingPositionList.splice(index1, 1);

        this.craftCardInfoDTOList[index].workingPositionList.splice(index1+1, 0, data)
      },
      //three
      addafter2(index,index1,index2){//向后添加
        let data = {
            "workingStepId": "",
            "tool": "",
            "cutter": "",
            "cutDepth": "",
            "mainshaftSpeed": "",
            "fixture": "",
            "feedingDepth": "",
            "load": "",
            "cutSpeed": "",
            "basicTime": "",
            "assistTime": "",
            "workspaceServiceTime": "",
            "sort": ""
          }
        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2+1, 0, data)
      },
      delcarts2(index,index1,index2){//删除卡片
        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2, 1)
      },
      removefront2(index,index1,index2){//向前移动
        let data = this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList[index2];
        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2, 1);
        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2-1, 0, data)
      },
      removeafter2(index,index1,index2){//向后移动
        let data = this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList[index2];
        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2, 1);

        this.craftCardInfoDTOList[index].workingPositionList[index1].workingStepList.splice(index2+1, 0, data)
      },
      viewwork(){
        this.$router.push({path:'workdetail'});
      },
      // 保存并提交
      submitwork() {
          this.$confirm('确定提交？提交完成不能修改', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.savework(1);
          })
          .catch(() => {});
      },
      deletemessage(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage(id, index) {//删除消息
        this.loading = true;
        try {
          let response = await fetchDeletestudentmessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Submits(){//提交作业
        this.loading = true;
        try {
          let response = await fetchSubmit(this.id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '作业提交成功',
              type: 'success'
            });
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async savework(type){//保存作业
        let {craftCardInfoDTOList, partNumber, partName, productType, materials, workblankWeight, workblankKind, id} = this;
        for(var a = 0;a<craftCardInfoDTOList.length;a++){
          craftCardInfoDTOList[a].sort=a+1;
          for(var b = 0;b<craftCardInfoDTOList[a].workingPositionList.length;b++){
            craftCardInfoDTOList[a].workingPositionList[b].sort=b+1;
            for(var c =0;c<craftCardInfoDTOList[a].workingPositionList[b].workingStepList.length;c++){
              craftCardInfoDTOList[a].workingPositionList[b].workingStepList[c].sort=c+1;
            }
          }
        }
        if (type!=2) {
          this.loading = true;
        }else{
          this.tishi = "正在自动保存...";
        }
        var that = this;
        try {
          let response = await fetchAddcardApi(id, partNumber, partName, productType, materials, workblankWeight, workblankKind, craftCardInfoDTOList);
          if (response.status.code ==0) {
            if (type==1) {
              this.Submits()
            }else if(type==0){
              this.$message({
                showClose: true,
                message: '作业保存成功',
                type: 'success'
              });
            }else{
              setTimeout(function (){that.tishi = "保存成功"},1500);
            }
            this.Getstudentcardbyworkid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getworkinfobyid() {//得到作业详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetstudentworkinfobyid(id);
          if (response.status.code ==0) {
            this.items = response.result;
            this.teachingid = response.result.teaching.id;
            this.Studentlistmessage2(1);
            // this.workMessageInfoDTOList = response.result.workMessageInfoDTOList;
            this.status = response.result.status;
            if (response.result.status==0||response.result.status==2) {
              this.timers();
            };
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getstudentcardbyworkid(){
        try {
          let response = await fetchGetstudentcardbyworkid(this.id);
          if (response.status.code ==0) {
            if (!response.result) {
              this.craftCardInfoDTOList = [{
                "processId": "",
                "surfaceId": "",
                "close":false,
                "processFileId": "",
                "processFile":{
                  'name': "",'path': "",'size': ""
                },
                "name": "",
                "sort": "",
                "workingPositionList": [{
                    "workingPositionId": "",
                    "sort": "",
                    "workingStepList": [{
                        "workingStepId": "",
                        "tool": "",
                        "cutter": "",
                        "cutDepth": "",
                        "mainshaftSpeed": "",
                        "fixture": "",
                        "feedingDepth": "",
                        "load": "",
                        "cutSpeed": "",
                        "basicTime": "",
                        "assistTime": "",
                        "workspaceServiceTime": "",
                        "sort": ""
                    }]
                }]
              }]
              
            }else{
              var process = response.result.processList;
              process.forEach((item,index)=>{
                if (index==0) {
                  item.close=false
                }else{
                  item.close=true
                }
                if (!item.processFile) {
                  item.processFile={'name': "",'path': "",'size': ""}
                };
              })
              this.craftCardInfoDTOList = process;
              this.partNumber=response.result.partNumber;
              this.partName=response.result.partName;
              this.productType=response.result.productType;
              this.materials=response.result.materials;
              this.workblankWeight=response.result.workblankWeight;
              this.workblankKind=response.result.workblankKind;
              // this.craftCardInfoDTOList = this.$sortBykey(response.result.craftCardInfoDTOList,'sort');
            }
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Addmessagebystudent(talktext) {//添加作业消息
        let {id} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddmessagebystudent(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Studentlistmessage(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore(pagenum){
        this.Studentlistmessage(pagenum)
      },
      async Studentlistmessage(pageNum) {//得到私信消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList=[];
          this.startId=0;
          var size=5;
        };
        try {
          let response = await fetchStudentlistmessage(this.id, pageNum, 5, this.startId);
          if (response.status.code ==0) {
            this.totalsi = response.result.total;
             this.workMessageInfoDTOList=response.result.list.reverse().concat(this.workMessageInfoDTOList)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      async Finishedsurface() {//得到加工表面
        try {
          let response = await fetchFinishedsurface(this.id);
          if (response.status.code ==0) {
            this.biaolist = response.result.list;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
    }
};
</script>

